  src: url('../fonts/NotoSansCJKjp-Medium.eot?#iefix') format("embedded-opentype"), url('../fonts/NotoSansCJKjp-Medium.woff') format("woff"), url('../fonts/NotoSansCJKjp-Medium.ttf') format("truetype"); }


