@import url('//fonts.googleapis.com/css?family=Lora:700i');

$font__primary: YakuHanJP, 'Noto Sans Japanese', sans-serif;
$font__accent: 'Lora', serif;


$color-primary: '30, 46, 83';
$color-primary-dark: '255, 79, 0';
$color-primary-light: '255, 247, 242';

$color-accent: '172, 135, 61';


$color-facebook: '59, 89, 152';
$color-twitter: '85, 172, 238';



